</template>
<style scoped>
  .boldText{
    font-weight:bold;
  }
  .row.especiallyRow{
    margin-bottom:30px
  }
  .textCard p{ font-size: 14px;line-height: 30px;padding-top:10px;}
  .textCardChard p{ padding: 5px;padding-left: 20px;font-size: 12px;}
  .textCard h4{padding-top: 30px;font-weight: bold}
  .textCardChard p{ padding: 5px;padding-left: 20px;font-size: 12px;line-height: 25px;}
