  .payBox{ width:400px;margin: auto;margin-bottom: 30px;cursor: pointer}
  .check{ width:400px;margin: auto;margin-bottom: 30px;}
  i.rede{ color: red }
  .sucsse{padding-top:20px;font-size: 16px;font-weight:bold;padding-bottom: 20px; }
  .goWay{color:#6886d4}
  .goWay span{margin: 0px 10px ;}
