  .invalid{width: 500px;margin: auto;padding: 50px;}
  .invalid p{font-size: 24px;font-weight: bold}
</style>

<script>
  import CommonHeader from '../../public/header.vue';
  import Setting from '../../public/set.vue';
  import publicFn from '../../../untils/Public.js';
  import code from '../../../untils/Qrcode.js'
  //    import NavTop1 from '../public/nva1.vue';
  // import NavTop from '../public/navTop.vue';
  //    import CertOderCent from '../cretOder/component/certOderCent.vue';

  export default {
    //判斷是否已經登陸
    created:function(){
//            if(!localStorage.getItem('access_token')){
//                this.$router.push({ name:"index" })
//            }
    },
    mounted:function() {
      var id=this.$route.query.id;
      this.id=id;
      var userId=this.$route.query.userId;
      this.usereId=userId;
      var type = this.$route.query.type;
      this.type = type;
      this.lists();
      this.OderMage();
      var that=this;
      this.timeForOder= setInterval(function(){
        that.inst()
      },5000);
    },
    name: 'CertOder',
    data:function() {
      return{
        massge: {
          cunt:0,
          list:[{}]
        },
        OrderChart:[],
        usereId:"",
        id:"",
        oder:null,
        status:0,//未付款0  付款完成 1 ，付款失败2
        //开启计时
        timeFor120:true,
        //开启轮询
        timeForOder:true,
        //倒计时
        timeOutS:{},
        //判断从那边过来
        type:null,
        //订单是否失效
        invalid:false
      }
    },
    methods:{
      lists:function(){
        var id = this.id;
//          var parm={ pageNumber:1,pageSize:10,search:id}
        var url='api/assist-order/assistOrders/'+id;
        var token=localStorage.getItem('access_token');
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer '+token
          }
        }).then(function(res){
          var res=res.body;
          var startTime=res.createdWhen;
          var time=117;//多久
          var that=this;
          this.timeFor120= setInterval(function(){
            var times= publicFn.publicLo.tmieOut(startTime,time);
            that.timeOutS=times;
            if( times.timeM<=0){
              clearInterval(that.timeFor120);
              clearInterval(that.timeForOder);
              that.warning("订单已失效");
              that.invalid=true;
            }
          },1000);
          var date= {
            expertName:res.expertName,
            doctorTitle:res.doctorTitle,
            doctorName:res.doctorName,
            doctorId:res.doctorId,
            price:res.totalPrice,
            type:res.type,
            recordId:res.recordId,
            hospitalId:res.hospitalId,
            patientName:res.patientName,
            description:res.res,
            expertTitle:res.expertTitle,
            startTime:publicFn.publicLo.timeCheng(res.startTime),
            id:res.id,
            scheduleId:res.scheduleId,
            skuId:res.skuId,
            orderNo:res.orderNo,
            recordType:res.recordType,
            sex:res.sex,
            paidIn:res.paidIn,
            usersId:res.usersId,
            endTime:publicFn.publicLo.timeCheng(res.endTime),
            age:res.age,
            status:res.status,
            orderTypeValue:res.orderTypeValue,
            expertImageUrl:res.expertImageUrl
          };
          switch (date.recordType){
            case 1:
              date.recordType="高血压";
              break;
            case 2:
              date.recordType="心电生理";
              break;
            case 3:
              date.recordType="冠脉血管" ;
              break;
            case 4:
              date.recordType="心力衰竭";
              break;
            default :
              date.recordType="---";
              break
          }
          date.type="图文问诊"
          if(date.type==0){
            date.type="图文问诊"
          }
          if(date.type==1){
            date.type="视屏面诊"
          }
          if(date.sex==1){
            date.sex="男";
          }
          if(date.sex==2){
            date.sex="女";
          }
          date.endTime=date.endTime.hms ;
          date.startTime=date.startTime.times ;
          this.OrderChart = date;
          if(!res.isPaid){

          }else if( res.isPaid==1){
            this.status=1;
          }
        });
      },
      backPatient:function(){
      },
      pay:function(a){
        var orderNo=this.id;
        if(a==1){//微信
          var url='api/assistOrders/notify'+orderNo;
          this.$http.post(url ,{
            headers:{
              Authorization:'bearer' + token
            },
            emulateJSON:true
          }).then(function(res){
            res=res.body;});
        }
        if(a==2){
          //支付宝
          alert("暂时只支持微信");
//            var url='api/assistOrders/notify'+orderNo;
//            this.$http.post(url ,{
//              headers:{
//                Authorization:'bearer' + token
//              },
//              emulateJSON:true
//            }).then(function(res){
//              res=res.body;});
        }
      },
      payWeiXin:function(a){
        var url="/assist-order/assistOrders/+"+a+"/pay"
        this.$http.get(url,{
          headers:{
            Authorization:'bearer '+token
          }
        }).then(function(res) {
          var res = res.body;
          this.expertCharts = res
        })
      },
      //修改订单
      orderUpdata:function(){

      },
      //获取二维码
      OderMage:function(){
        var oderId=this.id;
        var url="api/assist-order/assistOrders/+"+oderId+"/pay";
        this.$http.get(url,{
        }).then(function(res) {
          res=res.body;
          this.oder=res;
          this.certMage(document.getElementById('qrcode'), res.codeUrl);
        })

      },
      //生成二维码
      certMage:function(dom,url){
        if (!dom) return;
        if (!/weixin/.test(url)) return;
        if (this.qrcode) {
          this.qrcode.clear();
          this.qrcode.makeCode(url);
        } else {
          this.qrcode = new QRCode(dom,{
            text:url,
            width: 300,
            height: 300,
            colorDark: '#000000',
            colorLight: '#ffffff',
            correctLevel: QRCode.CorrectLevel.H,
          });
        }
      },
      //轮询订单状态
      inst:function(){
        var id =this.id;
        var url='api/assist-order/assistOrders/'+id;
        var token=localStorage.getItem('access_token');
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer '+token
          }
        }).then(function(res){
          var res=res.body;
          if(!res.isPaid){

          }else if( res.isPaid==1){
            this.status=1;
            //付款成功 清除倒计时和轮询
            clearInterval(this.timeForOder);
            clearInterval(this.timeFor120);
          }
        });
      },
      back:function(a){
        clearInterval(this.timeForOder);
        clearInterval(this.timeFor120);
        if(this.type==1){
          this.$router.push({name: "Expert"})
        }else{
          window.history.back()
        }
      },
      goExpert:function(){
        clearInterval(this.timeForOder);
        clearInterval(this.timeFor120);
        this.$router.push({name: "Expert"})
      },
      goAssisOders:function(){
        clearInterval(this.timeForOder);
        clearInterval(this.timeFor120);
        this.$router.push({name: "AssistOrders"})
      },
      open:function(text) {
        this.$message(text);
      },
      success:function(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      warning:function(text){
        this.$message({
          message: text,
          type: 'warning'
        });
      },
    },
    components: {
      CommonHeader: CommonHeader,
      Setting:Setting,
//            CertOderCent:CertOderCent
    }
  }
</script>



